.about {
  &__title {
    margin: 0 0 48px;
    font-weight: bold;
    font-size: 36px;
    line-height: 36px;
    letter-spacing: -1.5px;

    color: #000;

    @include onDesktop {
      margin-bottom: 0;

      font-size: 48px;
      line-height: 48px;
    }
  }

  &__title-link {
    color: $c-blue;
    text-decoration: none;

    transition: color 0.5s;

    &:hover {
      color: #005ecc;
    }
  }

  &__subtitle {
    display: block;
    margin-bottom: 16px;

    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    text-decoration: none;
    color: $c-blue;

    transition: color 0.5s;

    &:hover {
      color: #005ecc;
    }
  }

  &__paragraph {
    margin: 0 0 27px;

    &:last-child {
      margin: 0;
    }

    &--important {
      font-weight: bold;
    }
  }
}
